import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import Loading from './Loading';
import loadingImg from '../Spinner.gif';
import './ProductsList.css';

class Homepage extends Component {
	constructor(props) {
		super(props);
		this.state = {
			dataLoaded: false,
			beersData: [],
			productNameHeight: null
		}		
	}

	// updateProductNameHeight = () => {
	// 		const el = ReactDOM.findDOMNode(this);
	// 		const beerName = [...el.querySelectorAll('.beer-name')];
	// 		const height = beerName.reduce((a, c) => {
	// 			if (c.scrollHeight - a > 0) {
	// 				return c.scrollHeight;
	// 			}
	// 			else {
	// 				return a;
	// 			}
	// 		},0);
	// 		this.setState({productNameHeight: height});
	// 		console.log('Resize detected');
	// }

	componentDidMount() {
		const newBeers = 'https://api.punkapi.com/v2/beers?per_page=18';

	    axios.get(newBeers)
		  .then(res => res.data)
		  .then(result => {
		    this.setState({
		    	dataLoaded: true,
		    	beersData: result,
		    })
		  })
		  .catch(err => {
		    console.log(err);
		  });

		// window.addEventListener("resize", this.updateProductNameHeight);
	}

	// componentDidUpdate(prevProps, prevState) {
	// 	const el = ReactDOM.findDOMNode(this);
	// 	const beerName = [...el.querySelectorAll('.beer-name')];
	// 	const height = beerName.reduce((a, c) => {
	// 		if (c.scrollHeight - a > 0) {
	// 			return c.scrollHeight;
	// 		}
	// 		else {
	// 			return a;
	// 		}
	// 	},0);

	// 	if ( prevState.beersData !== this.state.beersData || prevState.productNameHeight !== this.state.productNameHeight ) {
	// 		this.setState({productNameHeight: height});
	// 		console.log('Height changed');
	// 	}

	// }


    render() {
    	const {dataLoaded, beersData, productNameHeight} = this.state;
    	console.log(productNameHeight)
    	const inlineStyle = {
    		height: productNameHeight === null ? 'auto' : productNameHeight
    	};
    	const beers = beersData.map(beer =>
    		(
    			<div key={beer.id} className="column is-one-quarter ListedProduct">
    				<div className="ListedProduct-inner">
    					<figure className="image is-128x128">
    										  <img src={beer.image_url ? beer.image_url : loadingImg } alt={beer.name}/>
    										</figure>
    					<div style={inlineStyle} className="beer-name">{beer.name}</div>
    				</div>
    			</div>
			)
		);


    	if (dataLoaded) {


	        return (
	        	<div>
	        		<div className="title has-text-centered">Our new products:</div>
		            <div className="columns is-multiline is-three-quarters-mobile">
		                {beers}
		            </div>
	        	</div>
	        );
    	}
    	else {
    		return <Loading />
    	}

    }
}

export default Homepage;


